@font-face {
  font-family: "Iceberg";
  src: url("../../vendor/Iceberg-Regular.woff") format("woff");
}

.GameField {
  z-index: 1;
  width: 43vw;
  height: 43vw;
  padding: 2vw;
  margin: 0;
  margin-right: 26.5vw;
  display: grid;
  grid-template-rows: repeat(4, 10vw);
  grid-template-columns: repeat(4, 10vw);
  grid-gap: 1vw;
  grid-template-areas:
    "top1 top2 top3 top4"
    "second1 second2 second3 second4"
    "third1 third2 third3 third4"
    "bottom1 bottom2 bottom3 bottom4";
  font-family: "Iceberg", sans-serif;
  font-weight: bold;
}

@media (max-width: 768px) {
  .GameField {
    width: 82vw;
    height: 82vw;
    margin: auto;
    grid-template-rows: repeat(4, 19vw);
    grid-template-columns: repeat(4, 19vw);
    grid-gap: 2vw;
  }
}

@media (max-width: 444px) {
  .GameField {
    margin-bottom: 2em;
  }
}
